// 
// maintenance
//

.maintenance-bg {
    background: var(--#{$prefix}body-bg);
}

.maintenance-pages {
    align-items: center;
    display: flex;
    min-height: 100vh;
    .error-section {
        .error-code {
            font-size: 11rem;
            font-weight: 800;
            background: linear-gradient(to bottom, #5dd89e, #1f2028);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            opacity: 0.9;
            text-shadow: 10px 10px rgba($body-bg, 0.15);
        }
        .shield-icon {
            font-size: 11rem;
            color: #1f2028;
            opacity: 0.9;
            text-shadow: 10px 10px rgba($body-bg, 0.15);
        }
    }
}

.maintenance-img {
    max-width: 350px;
    margin: auto;
}

.error-page {
    max-width: 450px;
    margin: auto;
}

.bg-svg {
    background-size: cover;
    background-position: center center;
}

.coming-soon-img {
    max-width: 500px;
    margin: auto;
}

@media (max-width: 767px) {
    .error-page {
        max-width: 300px;
        margin: auto;
    }
    .maintenance-pages {
        .error-section {
            .error-code {
                font-size: 8rem;
            }
            .shield-icon {
                font-size: 7rem;
            }
        }
    }
}