// 
// buttons.scss
//


.btn-icon.btn-sm {
    width: 1.75rem;
    height: 1.75rem;
    font-size: 0.8rem;
    padding: 0.1875rem;
    border: none;
    border-radius: 5px !important;
    color: var(--#{$prefix}muted) !important;
    background-color: var(--#{$prefix}light);
    &:hover {
        color: var(--#{$prefix}primary) !important;
        background-color: rgba($primary, 0.15) !important;
    }
    &:active,
    &:focus {
        border: none !important;
    }
}

.btn-light {
    background-color: var(--#{$prefix}light) !important;
    color: var(--#{$prefix}dark);
    border: none;
}

.btn-de-secondary {
    background-color: transparent !important;
    border: 1px solid #e8ebf3;
    color: var(--#{$prefix}dark);
    &:hover {
        background-color: rgba($primary, 0.10) !important;
        color: var(--#{$prefix}primary) !important;
        border: 1px solid rgba($primary, 0.10);
    }
}

.btn-email {
    display: flex;
    width: 2.25rem;
    height: 2.25rem;
    font-size: 0.8rem;
    align-items: center;
    justify-content: center;
    color: var(--#{$prefix}dark) !important;
    &:hover {        
        border-radius: 0.325rem;
        background-color: $light !important;
        border: 1px solid rgba($light, 0.10) !important; 
    }
    &:active {
        border: none;
    }
}

.btn-file {
    overflow: hidden;
    position: relative;
    vertical-align: middle;
    display: inline-block;
}

.btn-file input {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 0;
    filter: alpha(opacity = 0);
    font-size: 1.5rem;
    height: 100%;
    width: 100%;
    direction: ltr;
    cursor: pointer;
}