//
// Calendar.scss
//

// Table borders inside FullCalendar
.fc td,
.fc th {
    border: $table-border-width solid var(--#{$prefix}light);
}

// Main FC styles
.fc {
    .fc-toolbar {
        h2 {
            font-size: 16px;
            line-height: 30px;
            font-weight: 600;
            text-transform: uppercase;
        }

        .btn {
            text-transform: capitalize;
            @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $btn-font-size-sm, $btn-border-radius-sm);
        }

        @media (max-width: 767.98px) {
            .fc-left,
            .fc-right,
            .fc-center {
                float: none;
                display: block;
                text-align: center;
                clear: both;
                margin: 10px 0;
            }
            > * > * {
                float: none;
            }
            .fc-today-button {
                display: none;
            }
        }

        @media (max-width: 575.98px) {
            flex-direction: column;
        }
    }

    th.fc-widget-header {
        background: $gray-300;
        line-height: 20px;
        padding: 10px 0;
        text-transform: uppercase;
        font-weight: $font-weight-bold;
    }

    .fc-button-primary {
        background-color: var(--#{$prefix}light);
        border-color: var(--#{$prefix}light);
        color: var(--#{$prefix}muted);
        box-shadow: none;
        text-transform: capitalize;

        &:hover,
        &:focus {
            background-color: var(--#{$prefix}light);
            border-color: var(--#{$prefix}light);
            color: var(--#{$prefix}muted);
            box-shadow: none !important;
        }

        &:not(:disabled).fc-button-active,
        &:not(:disabled):active {
            background-color: var(--#{$prefix}primary);
            border-color: var(--#{$prefix}primary);
            color: var(--#{$prefix}white);
        }
    }

    .fc-scrollgrid {
        border-color: var(--#{$prefix}border-color) !important;
    }

    .fc-daygrid-day.fc-day-today {
        background-color: $info-bg-subtle !important;
    }

    .fc-col-header-cell-cushion,
    .fc-daygrid-day-number {
        color: var(--#{$prefix}body-color);
    }

    // Event styles
    .fc-event {
        border-radius: 2px;
        border: none;
        cursor: move;
        font-size: 0.8125rem;
        padding: 4px;
        text-align: center;
        justify-content: center;

        .fc-content {
            color: var(--#{$prefix}white);
        }

        .fc-event-title {
            flex-grow: 0 !important;
            font-weight: 500;
        }
    }

    .fc-daygrid-event-dot {
        border: none;
    }

    .fc-h-event .fc-event-main {
        color: var(--#{$prefix}primary);
    }
}

// Unthemed FC
.fc-unthemed {
    .fc-content,
    .fc-divider,
    .fc-list-heading td,
    .fc-list-view,
    .fc-popover,
    .fc-row,
    tbody,
    td,
    th,
    thead {
        border-color: $gray-300;
    }

    td.fc-today {
        background: lighten($gray-200, 2%);
    }
}

// Toolbar button chunk
.fc-toolbar-chunk {
    .fc-button-group {
        .fc-button {
            background: var(--#{$prefix}light);
            border-color: var(--#{$prefix}light);
            box-shadow: none;
            color: var(--#{$prefix}muted);
            text-transform: capitalize !important;
            padding: 6px 12px !important;
            height: auto !important;

            &.fc-today-button.fc-button-primary {
                background-color: $primary-bg-subtle !important;
                color: $primary !important;
            }
        }
    }
}

// Event dots
.fc-event-dot {
    background-color: $primary;
}

// Sidebar external events
#external-events {
    .external-event {
        text-align: left;
        padding: 8px 16px;
    }
}

// RTL Support
[dir="rtl"] {
    .fc-header-toolbar {
        direction: ltr !important;
    }

    .fc-toolbar > * > :not(:first-child) {
        margin-left: 0.75em;
    }
}

// List view day cushion
.fc-theme-standard {
    .fc-list-day-cushion {
        background-color: var(--#{$prefix}tertiary-bg) !important;
    }

    .fc-list {
        border: var(--#{$prefix}border-color) !important;
    }
}

// Event color variations
.event-primary {
    background-color: $primary-bg-subtle !important;
    color: var(--#{$prefix}primary) !important;
    border-left: 4px solid $primary !important;
}
.event-secondary {
    background-color: $secondary-bg-subtle !important;
    color: var(--#{$prefix}secondary) !important;
    border-left: 4px solid $secondary !important;
}
.event-danger {
    background-color: $danger-bg-subtle !important;
    color: var(--#{$prefix}danger) !important;
    border-left: 4px solid $danger !important;
}
.event-warning {
    background-color: $warning-bg-subtle !important;
    color: var(--#{$prefix}warning) !important;
    border-left: 4px solid $warning !important;
}
.event-info {
    background-color: $info-bg-subtle !important;
    color: var(--#{$prefix}info) !important;
    border-left: 4px solid $info !important;
}
.event-dark {
    background-color: $dark-bg-subtle !important;
    color: var(--#{$prefix}primary) !important;
    border-left: 4px solid $primary !important;
}
.event-sidebar-dark {
    background-color: var(--#{$prefix}event-dark) !important;
    color: var(--#{$prefix}event-dark-color) !important;
}
