// 
//  Authentication
// 
.account-page {
    align-items: center;
    display: flex;
    background: var(--#{$prefix}body-bg);
    .account-page-bg{
        background-size: cover;
        background-position: center center;
        min-height: 890px;
        display: flex;
        justify-content: center;
        .auth-image {
            max-width: 800px;
            margin: auto;
        }
    }
    .login-pattern {
        align-items: center;
        display: flex;
        position: relative;
    }
}
.auth-title-section {
    h3 {
        font-size: 25px;
    }
}

.header-box {
    background-color: $primary;
}
.auth-button a{
    color: $dark;
    font-weight: 500;
    border: 1px solid var(--bs-gray-400);
}

.saprator {
    position: relative;
    text-align: center;
    &::before {
        content: "";
        position: absolute;
        left: 0;
        width: 110px;
        top: 50%;
        height: 1px;
        background: #e5eaef;
    }
    &::after {
        content: "";
        position: absolute;
        right: 0;
        width: 110px;
        top: 50%;
        height: 1px;
        background: #e5eaef;
    }
}

.auth-user-review {
    color: var(--#{$prefix}body-bg);
    text-align: center;
    padding: 0 1.75rem;
    margin: 0px auto;
    .carousel-images {
        margin: 0px auto;
        max-width: 400px;
    }
    .prelead {
        margin: 0px auto;
        font-size: 1.125rem;
        text-align: center;
        max-width: 600px;
    }
}

.body-color {
    background-image: url('../images/bg-2.png');
    background-size: cover;
    background-position: center center;
    display: flex;
    justify-content: center;
}

.auth-brand {
    span.logo-lg {
        display: block;
    }
    .logo-light {
        display: none;
    }
}