// 
// Pricing.scss
//

.pricing-box {
    position: relative;
    .inner-box {
        position: relative;
    }
    .gradient-class {
        background: linear-gradient(to bottom, #f8f9ff, #e9ebff);
    }
    .plan-title {
        font-size: 21px;
        margin-bottom: 16px;
        color: $dark;
        text-transform: capitalize;
        font-weight: 600;
    }
    .plan-subtitle {
        font-size: 14px;
        margin-bottom: 28px;
        color: #{map-get($grays, "600")};
    }
    .plan-stats {
        margin-bottom: 0px;
        li {
            font-size: 15px;
            margin-bottom: 14px;
        }
        .check-icon {
            width: 15px;
            height: 15px;
        }
    }
}

.plan-bg {
    background: var(--#{$prefix}body-bg);
    padding: 15px;
}

.plan-header-2 {
    .pricing-box-2 {
        max-width: 1300px;
        .plan-title-2 {
            background: var(--#{$prefix}body-bg);
            padding: 6px 10px;
            font-size: 14px;
            margin-bottom: 0px;
            color: var(--#{$prefix}white);
            text-transform: capitalize;
            font-weight: 600;
            display: inline-block;
        }
    }
}
