//
// Dashboards
//

.jobs-icons {
  height: 18px;
  width: 18px;
}

.avatar-list-stack {
  .avatar {
    margin-inline-end: -0.65rem !important;
  }
}

.dashboard-badge {
  padding: 2px 5px 2px 5px;
}

.dashboard-icons {
  font-size: 30px;
}

.custom-legend .legend-item {
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 13px;
  color: var(--#{$prefix}text-muted);
}

.custom-legend .legend-item.inactive {
  opacity: 0.5;
}

.legend-dot {
  width: 7px;
  height: 7px;
  border-radius: 7px;
  display: inline-block;
}

.bg-muted-color {
  background-color: rgb(var(--#{$prefix}body-bg)) !important;
}

.widget-icon-box {
  border-radius: 30%;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}