// 
// FAQs
// 

.faq-icon-box {
    align-items: center;
    justify-content: center;
    min-width: 35px;
    height: 35px;
    width: 35px;
    border-radius: 5px;
    float: left;
    display: flex;
    border: 1px solid $primary;
}

.search-faqs {
    max-width: 350px;
    margin: 0px auto;
}

.faq-icons {
    height: 40px;
    width: 40px;
    line-height: 40px;
    display: flex;
    align-items: center;
    background: var(--#{$prefix}body-bg);
    justify-content: center;
}

.f-question {
    margin-top: 0;
    margin-left: 60px;
    font-weight: 600;
    font-size: 16px;
    color: $dark;
}
.f-answer {
    margin-left: 60px;
}
.icon-md {
    height: 16px;
    min-width: 16px;
}
.accordion-item-faq {
    background-color: var(--#{$prefix}light);
    margin-bottom: 15px;
    border-radius: 5px;
    padding: 15px 18px;
    display: block;
    .accordion-button {
        background-color: var(--#{$prefix}light);
        padding: 0px;
        font-size: 16px;
        box-shadow: none;
        color: $dark;
    }
    .accordion-body {
        padding: 20px 0px;
    }
}

.accordion-item-faq:not(:first-of-type) {
    border: var(--#{$prefix}accordion-border-width) solid var(--#{$prefix}accordion-border-color);
}

.profile-container{
    background: var(--#{$prefix}sidebar-item-active-bg);
    border-radius: 5px;
}
