// 
// topbar.scss
//

.topbar-custom {
    background-color: var(--#{$prefix}topbar-bg);
    padding: 0 calc(#{$grid-gutter-width} / 2) 0 0;
    position: fixed;
    left: $sidebar-width;
    right: 0;
    height: $topbar-height;
    box-shadow: $box-shadow-sm;
    z-index: 1000;
    transition: all .1s ease-out;
    .topnav-menu {
        .nav-link {
            padding: 0px 0px 0px 10px;
            color: var(--#{$prefix}topbar-menu-nav-link-color);
            display: block;
            line-height: $topbar-height;
            text-align: center;
            max-height: $topbar-height;
            position: relative;
        }
        .topbar-search {
            padding: 0 15px;
            .form-control {
                border-color: var(--#{$prefix}border-color);
                width: 250px;
                padding-left: 35px;
                border-radius: 0.325rem;
                transition: width 0.3s ease;
                &:focus {
                    width: 400px;
                }
            }
        }
        .topbar-button{
            border-radius: 0.325rem;
            padding: .5rem .5rem;
            background-color: var(--#{$prefix}body-bg);
            color: var(--#{$prefix}sidebar-item);
            border: 1px solid var(--#{$prefix}border-color);
        }
    }
    /* Search */
}

/* Notification */
.noti-scroll {
    max-height: 400px;
    overflow-y: auto;
    .badge-custom {
        background-color: var(--#{$prefix}secondary-bg);
        border: 1px solid var(--#{$prefix}border-color);
        padding: .4rem .4rem !important;
    }
}

.notification-list {
    margin-left: 0;
    .dropdown-notifications {
        background: var(--#{$prefix}secondary-bg);
        .notification-tabs {
            padding: 0px 20px;
            .nav-tabs-custom {
                background: var(--#{$prefix}body-bg);
                border-radius: 0.325rem;
                padding: 5px;
                .nav-item {
                    display: flex;
                    align-items: center;
                    .nav-link-tab {
                        padding: 6px 8px;
                        font-size: 14px;
                        color: var(--#{$prefix}muted);
                        border-radius: 0.325rem;
                        &.active {
                            background: var(--#{$prefix}secondary-bg);
                            color: var(--#{$prefix}dark);
                            border: 1px solid var(--#{$prefix}border-color);
                            box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .05);
                            display: inline-block;
                        }
                    }
                    &.active,
                    &:active {
                        border-radius: 5px;
                    }
                }
            }
        }
    }
    .noti-title {
        background-color: transparent;
        padding: 15px 20px;
    }
    .noti-icon-badge {
        display: inline-block;
        position: absolute;
        top: 16px;
        right: 10px;
    }
    .notify-item {
        padding: 12px 20px;
        .notify-content {
            margin-left: 45px;
        }
        .noti-mentioned {
            background-color: var(--#{$prefix}noti-mentioned-bg);
        }
        .notify-icon {
            float: left;
            height: 36px;
            width: 36px;
            font-size: 18px;
            line-height: 36px;
            text-align: center;
            margin-right: 10px;
            border-radius: 5px;
            color: $white;
        }
        .notify-sub-icon {
            height: 36px;
            width: 36px;
            font-size: 18px;
            line-height: 36px;
            text-align: center;
            margin-right: 10px;
            border-radius: 5px;
            color: $white;
            background-color: var(--#{$prefix}noti-mentioned-bg);
        }
        .notify-details {
            margin-bottom: 5px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-weight: 500;
            color: var(--#{$prefix}body-color);
            b {
                font-weight: 500;
            }
            small {
                display: block;
            }
            span {
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 13px;
            }
        }
        .user-msg {
            white-space: normal;
            line-height: 16px;
        }
    }
    .tab-pane-notification {
        padding: 12px 20px;
        .notify-item-data {
            padding: 12px 0px;
            &:hover {
                background: none;
            }
            .notify-content {
                margin-left: 45px;
            }
            .noti-mentioned {
                background-color: var(--#{$prefix}noti-mentioned-bg);
            }
            .notify-icon {
                float: left;
                height: 36px;
                width: 36px;
                font-size: 18px;
                line-height: 36px;
                text-align: center;
                margin-right: 10px;
                border-radius: 5px;
                color: $white;
            }
            .notify-sub-icon {
                height: 36px;
                width: 36px;
                font-size: 18px;
                line-height: 36px;
                text-align: center;
                margin-right: 10px;
                border-radius: 5px;
                color: $white;
                background-color: var(--#{$prefix}noti-mentioned-bg);
            }
            .notify-details {
                margin-bottom: 5px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-weight: 500;
                color: var(--#{$prefix}body-color);
                b {
                    font-weight: 500;
                }
                small {
                    display: block;
                }
                span {
                    display: block;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 13px;
                }
            }
            .user-msg {
                white-space: normal;
                line-height: 16px;
            }
        }
    }
    .profile-dropdown {
        .notify-item {
            padding: 7px 20px;
        }
    }
}

.profile-dropdown {
    min-width: 170px;
    iconify-icon {
        vertical-align: middle;
        margin-right: 5px;
    }
    i {
        vertical-align: middle;
        margin-right: 5px;
    }
}

.nav-user {
    padding: 0 15px !important;
    img {
        height: 34px;
        width: 34px;
        padding: 2px;
        border: 1px solid var(--#{$prefix}primary);
        object-fit: cover;
        border-radius: 50%;
    }
}

.page-title-main {
    margin: 0;
    line-height: 70px;
    padding: 0 24px;
}

.light-mode {
    display: inline;
}

.dark-mode {
    display: none;
}

// Dark Mode Icon
html[data-bs-theme="dark"] {
    .topbar-custom {
        #light-dark-mode {
            i {
                &::before {
                    transition: all 0.3s ease-in-out;
                    content: "\eebe";
                }
            }
            .light-mode {
                display: none;
            }
    
            .dark-mode {
                display: inline;
            }
        }
    }   
}

@include media-breakpoint-down(lg) {
    .logo-box {
        width: $sidebar-width-sm !important;
        padding-right: 0 !important;
        .logo-lg {
            display: none !important;
        }
        .logo-sm {
            display: block !important;
        }
    }
}

@media (max-width: 600px) {
    .topbar-custom {
        .dropdown {
            position: static;
            .dropdown-menu {
                left: 10px !important;
                right: 10px !important;
            }
        }
    }
}

@media (max-width: 360px) {
    .topbar-custom {
        .topnav-menu {
            .nav-link {
                padding: 0 12px;
            }
        }
        .button-menu-mobile {
            width: 45px;
        }
    }
}

@media (max-width: 992px) {
    .button-menu-mobile.disable-btn {
        display: block !important;
    }
    .page-title-main{
        display: none;
    }
}