// 
// File Manager
//

.file-container {
    .file-sidebar {
        .fileMenu-list {
            .nav-link {
                border-radius: 0.375rem;
                padding: 0.55rem 0.8rem;
                position: relative;
                display: flex;
                align-items: center;
                font-weight: 500;
                &.active {
                    color: var(--#{$prefix}primary);
                    background-color: rgba($primary, 0.10);
                }
                &:hover {
                    color: var(--#{$prefix}primary);
                }
            }
        }
    }

    // File List
    .all-file {
        width: 100%;
    }
}

@media (min-width: 992px) {
    .file-sidebar {
        min-width: 20rem;
        max-width: 20rem;
    }
}

@media (min-width: 767px) and (max-width: 1023.98px) {
    .file-sidebar {
        float:none;
        min-width: 20rem;
    }

    .all-file {
        margin: 0
    }
}

@media (max-width: 568px) {
    .file-sidebar {
        float: none;
        width: 100%;
    }
}