// 
// Vector Map
// 

.jvm-tooltip {
    border-radius: 5px;
    background-color: var(--#{$prefix}primary);
    font-family: $font-family-sans-serif;
    box-shadow: none;
    padding: 5px 9px
}

.jvm-container text {
    font-family: $font-family-sans-serif;
    font-size: $font-size-sm;
    box-shadow: none;
    fill: var(--#{$prefix}secondary-color);
}

.jvm-zoom-btn {
    background-color: $dark;
}

// Google Map CSS
.google-maps {
    height: 420px;
}


#world-mapline {
  svg path {
    fill: url(#map-pattern);    
    stroke-width: 0.5;
  }
}

.jvm-container image {
    width: 16px !important;
    height: 16px !important;
}