// 
// Email
//

.email-container {
    .email-sidebar {
        .emailMenu-list {
            .nav-link {
                border-radius: 0.375rem;
                padding: 0.55rem 0.8rem;
                position: relative;
                display: flex;
                align-items: center;
                font-weight: 500;

                &:hover,
                &.active {
                    color: var(--#{$prefix}primary);
                }

                &.active {
                    background-color: rgba($primary, 0.10);
                }
                
            }
        }
    }

    // Total Mail List
    .all-email {
        width: 100%;
        .main-table {
            .mail-list {
                tbody {
                    tr {
                        cursor: pointer;
                    }
                }
            }
        }
    }
    
}

// Compose Email Model Design
.modal-content {
    .email-modal-header {
        padding: 1rem 1rem;
    }
    .modal-body {
        .form-control {
            padding: 0.375rem 0.75rem;
            &:focus {
                border-color: var(--#{$prefix}primary) !important;
                color: var(--#{$prefix}dark) !important;
            }
        }
    }
}

// Offcanvas
.emailoffcanvas {
    background-color: var(--#{$prefix}secondary-bg);
    min-width: 50rem;
    .email-options-wrap {
        .vertical {
            width: 1px;
            height: 1.5rem;
            background: var(--#{$prefix}border-color);
            margin-left: .5rem;
            margin-right: .5rem; 
        }
    }
    .mail-content {
        flex: 1 1 0%;
    }

    .replay-box {
        .input-group {
            .form-control {
                &:focus {
                    border-color: var(--#{$prefix}primary) !important;
                }
            }
        }
    }
}

@media (min-width: 992px) {
    .email-sidebar {
        min-width: 20rem;
    }
}

@media (min-width: 767px) and (max-width: 1023.98px) {
    .email-sidebar {
        float:none;
        min-width: 20rem;
    }
    .emailoffcanvas {
        min-width: 30rem;
    }
    .all-email {
        margin: 0
    }
}

@media (max-width: 568px) {
    .email-sidebar {
        float: none;
        width: 100%;
    }
    .emailoffcanvas {
        min-width: auto;
    }
}