// 
// card.scss
//

.card {
    box-shadow: $box-shadow-sm;
    margin-bottom: $grid-gutter-width;
    border-radius: var(--#{$prefix}border-radius-lg) !important;
}

.card-body {
    border-radius: var(--#{$prefix}border-radius-lg);
}

.card-title {
    font-size: 15px;
    font-weight: 500;
}

.card-h-full {
    height: calc(100% - #{$grid-gutter-width});
}