//
// avatar.scss (avatar thumbnail sizes)
//

.avatar-xs {
    height: 1.5rem;
    width: 1.5rem;
}

.avatar-sm {
    height: 2.25rem;
    width: 2.25rem;
}

.avatar-md {
    height: 3.5rem;
    width: 3.5rem;
}

.avatar-lg {
    height: 4.5rem;
    width: 4.5rem;
}

.avatar-xl {
    height: 6rem;
    width: 6rem;
}

.avatar-xxl {
    height: 7.5rem;
    width: 7.5rem;
}

.avatar-title {
    align-items: center;
    color: $white;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
}

.avatar-group {
    padding-left: 12px;

    .avatar-group-item {
        margin: 0 0 10px -12px;
        display: inline-block;
        border: 2px solid $card-bg;
        border-radius: 50%;
    }
}

.avatar-person {
    height: 42px;
    width: 42px;
}

.avatar-group {
    a {
        margin-left: -0.8rem;
    }
}

.avatar-badge {
    display: inline-block;
    position: absolute;
    top: -6px;
    right: -5px;
}

// Thumb Avatar
.thumb-xxl {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 80px;
    font-size: 32px;
    font-weight: 700;
}

.thumb-xl {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 60px;
    font-size: 24px;
    font-weight: 700;
}

.thumb-lg {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    font-size: 14px;
    font-weight: 700;
}

.thumb-md {
    height: 36px;
    width: 36px;
}

.thumb-sm {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    width: 24px;
    font-size: 12px;
    font-weight: 700;
}

.thumb-xs {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    width: 20px;
    font-size: 10px;
    font-weight: 700;
}