// 
// Profile.scss
//

.profile-bg-picture {
    background-image: url("../images/bg-profile.svg");
}
.social-item {
    height: 1.5rem;
    width: 1.5rem;
    display: flex;
    line-height: 20px;
    border: 2px solid var(--#{$prefix}border-color);
    border-radius: 50%;
    color: $gray-500;
}
.profile-section {
    .profile-item {
        position: relative;
        padding-bottom: 24px;
        padding-left: 35px;
        .profile-icon {
            position: absolute;
            top: -6px;
            left: -20px;
        }
    }
}

.images-abstract {
    height: 300px;
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../images/small/abstract-images.jpg");
}

.list-inline-dots {
    .list-inline-item-second {
        &::before {
            content: "  ";
            margin-inline-end: .5rem;
        }    
    }
}

.Venix-main-sections {
    flex-direction: row;
    align-items: center;
}

.Venix-profile-main {
    position: relative;
    .sil-profile_main-pic-change {
        cursor: pointer;
        background-color: $primary;
        border-radius: 50%;
        width: 32px;
        height: 32px;
        position: absolute;
        left: -10px;
        top: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        box-shadow: 0 0 20px 0 rgba(250, 251, 251, .05);
        border: 3px solid #fff;
    }
}

.timeline {
    position: relative;
    .timeline-item {
        display: flex;
        align-items: flex-start;
        position: relative;
        padding-left: 60px;
        margin-bottom: 1.5rem;
        .timeline-icon {
            position: absolute;
            top: 0;
            left: 0;
            width: 38px;
            height: 38px;
            border-radius: 50%;
            border: 1px dashed var(--#{$prefix}border-color);
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #fff;
            z-index: 1;
        }
        .timeline-content {
            .timeline-title {
                font-size: 0.938rem;
            }
        }
        &:not(:last-child)::before {
        content: "";
            position: absolute;
            top: 44px;
            left: 18px;
            bottom: -1.5rem;
            width: 1px;
            border-left: 1px dashed var(--bs-gray-300);
            z-index: 0;
        }
    }
}