// 
// tables.scss
//

.table-traffic td {
  font-weight: 400;
  vertical-align: middle;
  font-size: 14px;
  padding: 11px 11px;
}

.table-traffic thead th {
  padding: 0px 11px;
  background-color: var(--#{$prefix}light);
  color: var(--#{$prefix}dark);
  padding-top: .5rem;
  padding-bottom: .5rem;
  font-weight: 500;
}

.table-traffic tbody tr:last-child td {
  border-bottom: none;
}

.table-traffic tr th:first-child,
.table-traffic tr td:first-child {
  padding-left: 1.25rem;
}

.table-traffic tr th:last-child,
.table-traffic tr td:last-child {
  padding-right: 1.25rem;
}

// 
// table stock
// 
.table-stock {
  thead th {
    padding: 0.75rem 11px;
    background-color: var(--#{$prefix}light);
    font-weight: 600;
    border-bottom: none;
  }

  tbody {
    tr {
      border-bottom: 1px dashed var(--#{$prefix}border-color);
      
      td {
        font: 400 14px inherit;
        vertical-align: middle;
        padding: 13px;
        border-bottom: 0 !important;
      }
    }
  }

  tr {
    th:first-child,
    td:first-child {
      padding-left: 1.25rem;
    }

    th:last-child,
    td:last-child {
      padding-right: 1.25rem;
    }
  }
}

// 
.table-card td {
  font-weight: 400;
  vertical-align: middle;
  font-size: 14px;
  padding: .75rem .6rem;
}

.table-card th {
  padding: .75rem .6rem;
}

.table-card thead th {
  font-weight: 500;
}

.table-card thead th:first-child {
  border-radius: 5px 0 0 5px;
}

.table-card thead th:last-child {
  border-radius: 0 5px 5px 0 !important;
}

.table thead th:last-child {
  border-radius: 0 0px 0px 0;
}


// Contact List
.table-contact-main {
  margin: 0 -20px;

  .table-contactlist {
    border-top-left-radius: 0.325rem;

    thead th {
      padding: 1rem 0;
      background-color: var(--#{$prefix}contact-list-header-color);
      color: var(--#{$prefix}sidebar-item);
      font-weight: 500;
    }

    tbody {
      tr:last-child td {
        border-bottom: none;
      }
    }

    th,
    td {
      font-size: 14px;
      font-weight: 400;
      vertical-align: middle;
      padding: 13px;

      &:first-child {
        padding-left: 1.2rem;
        border-top-left-radius: 0.325rem;
      }

      &:last-child {
        padding-right: 1.2rem;
        border-top-right-radius: 0.325rem;
      }
    }
  }
}

// HTML Dark and Light Mode
html[data-bs-theme=dark] .datatable-container thead, html[data-bs-theme=dark] .table-light {
  --#{$prefix}table-color: #ffffff;
  --#{$prefix}table-bg: #2a2b34;
  --#{$prefix}table-border-color: #ced4da;
  --#{$prefix}table-striped-bg: #363845;
  --#{$prefix}table-striped-color: #ffffff;
  --#{$prefix}table-active-bg: #ced4da;
  --#{$prefix}table-active-color: #ffffff;
  --#{$prefix}table-hover-bg: #3b3d4a;
  --#{$prefix}table-hover-color: #ffffff
}