// 
// chat
// 


// Chat box left 
.chat-box-left {
    float: left;
    width: 340px;
    height: 710px;
    margin-bottom: 20px;
    border-radius: 0.325rem;
    overflow: hidden;
    background-color: var(--#{$prefix}secondary-bg);

    // chat search
    .chat-search {
        .form-control {
            border: none;
            padding-left: 35px;
            border-radius: 25px;
            transition: width .3s ease;
            background-color: var(--#{$prefix}body-bg) !important;
        }
        .chat-btn {
            background: var(--#{$prefix}sidebar-item-active-bg) !important;
            border-color: var(--#{$prefix}sidebar-item-active-bg) !important;
        }
    }

    .chat-list {
        .chat-padding {
            position: relative; 
            padding: 0.6rem 1.250rem;
            transition: transform 0.5s ease;
            &:hover,
            &.active {
                transition: all .5s;
                background-color: rgba(93, 216, 158, .11);
            }
            &.active::after {
                content: "";
                top: 0;
                right: 0px;
                height: 56px;
                width: 3px;
                opacity: 1;
                position: absolute;
                border-radius: 3px;
                background-color: var(--#{$prefix}primary);
                transition: opacity 0.3s ease, background-color 0.3s ease;
            }
        }
    }

    // chat body side 
    .chat-body-left {
        height: 597px; // 634px
        margin-left: -19px;
        margin-right: -19px;
    }
}

// Chat box right
.chat-box-right {
    width: auto;
    height: 710px;
    display: block;
    position: relative;
    margin-left: 361px;
    margin-bottom: 20px;
    border-radius: 0.325rem;
    background-color: var(--#{$prefix}secondary-bg);
    .chat-header {
        background-color: var(--#{$prefix}body-bg);
    }
    .chat-footer {
        display: flex;
        left: 19.200px;
        padding: 1.2rem;
        bottom: 19.200px;
        right: 19.200px;
        position: absolute;
        align-items: center;
        border-radius: 0.325rem;
        background-color: var(--#{$prefix}body-bg);
        .chat-footer-button {
            &:active {
                border: none;
            }
        }
    }
    .chat-body {
        padding: 16px;
        height: 500px;
        .chat-detail {
            .chat-box-message {
                .user-chat {
                    p {
                        width: 75%;
                        padding: 14px;
                        margin-bottom: 5px;
                        max-width: max-content;
                        background-color: var(--#{$prefix}body-bg);
                        border-radius: 0.325rem 0.325rem 0.325rem 0rem;
                    }
                }
                .chat-timing {
                    font-size: 10px;
                }
            }
            .chat-box-message.reverse {
                display: flex;
                align-items: flex-end;
                flex-direction: column;
                .user-chat {
                    width: 100%;
                    display: contents;
                    p {
                        text-align: end;
                        color: var(--#{$prefix}secondary-bg);
                        background-color: rgba($primary, 0.75);
                        border-radius: 0.325rem 0.325rem 0rem 0.325rem;
                    }
                }
            }
        }
    }
}


// Media quary
@media (min-width: 1024px) and (max-width:1680px) {
    .chat-box-left {
        width:310px;
        float: left
    }

    .chat-box-right {
        width: auto;
        margin-left: 330px
    }
}

@media (min-width: 767px) and (max-width:1023.98px) {
    .chat-box-left {
        width:100%;
        float: none
    }

    .chat-box-right {
        width: 100%;
        margin-left: 0
    }
}

@media (max-width: 767px) {
    .chat-box-left {
        float:none;
        width: 100%
    }

    .chat-box-right {
        margin: 0;
        width: 100%
    }
}

@media (max-width: 660px) {
    .chat-box-left {
        float:none;
        width: 100%
    }

    .chat-box-right {
        margin: 0;
        width: 100%
    }
}

@media (max-width: 568px) {
    .chat-box-left {
        float:none;
        width: 100%
    }

    .chat-box-right {
        margin: 0;
        width: 100%
    }
}