// 
//  Contact List
// 

.contact-search {
    .form-control {
        border-color: var(--#{$prefix}border-color);
        width: 340px;
        padding-left: 35px;
        border-radius: .325rem;
        transition: width .3s ease;
        &:focus {
            box-shadow: var(--#{$prefix}box-shadow-sm) !important;
        }
    }
}

.contact-list-box {
    padding: 0.47rem;
}